import React, { useState, useCallback } from "react";
import Gallery from "react-photo-gallery";
import Modal from 'react-bootstrap/Modal';
import Carousel from 'react-bootstrap/Carousel';
import Container from 'react-bootstrap/Container';
import { Photos as photos } from "./Photos.js";
import './GallerySection.css';

function GallerySection() {
  const [currentImage, setCurrentImage] = useState(0);
  const [viewerIsOpen, setViewerIsOpen] = useState(false);

  const openLightbox = (photo) => {
    console.log("here", photo['index']);
    setCurrentImage(photo['index']);
    setViewerIsOpen(true);
  };

  const closeLightbox = () => {
    setCurrentImage(0);
    setViewerIsOpen(false);
  };

  return (
    <div>
    <Container className="custom-container">
      <h1 className="title-gallery-section">Gallery</h1>
        <Gallery photos={photos} onClick={(e, photo) => openLightbox(photo)} />
        <Modal 
          show={viewerIsOpen}
          onHide={closeLightbox}
          backdrop="static"
          size="xl">
          <Modal.Header closeButton></Modal.Header>
          <Modal.Body>
                <Carousel defaultActiveIndex={currentImage}>
                    {photos.map(x => (
                        <Carousel.Item>
                            <img className="d-block w-100" className="gallery-carousel" src={x.src} alt={"Resort Photo"} />
                            </Carousel.Item>
                    ))}
                    
                </Carousel>
            </Modal.Body>
        </Modal>
        </Container>
        
    </div>
  );
}

 export default GallerySection;